
import { defineComponent, ref, onMounted } from 'vue';
import ButtonExample from './button.vue';
import IconExample from './icon.vue';
import RadioExample from './radio.vue';
import TagExample from './tag.vue';
import SelectExample from './select.vue';
import FormExample from './form.vue';
import UploadExample from './upload.vue';

export default defineComponent({
  name: 'Examples',
  components: {
    ButtonExample,
    IconExample,
    RadioExample,
    TagExample,
    SelectExample,
    FormExample,
    UploadExample,
  },
  setup() {
    const componentName = ref('');
    const contentRef = ref<null | HTMLDivElement>(null);
    const components = [
      {
        name: 'IconExample',
        title: 'Icon 图标',
      },
      {
        name: 'ButtonExample',
        title: 'Button 按钮',
      },
      {
        name: 'TagExample',
        title: 'Tag 标签',
      },
      {
        name: 'RadioExample',
        title: 'Radio 单选框',
      },
      {
        name: 'SelectExample',
        title: 'Select 选择器',
      },
      {
        name: 'UploadExample',
        title: 'Upload 文件上传',
      },
      {
        name: 'FormExample',
        title: 'Form 表单',
      },
    ];
    const handleClick = (name: string) => {
      componentName.value = name;
      window.location.hash = `/${name}`;
      if (contentRef.value) {
        contentRef.value.scrollTo({
          top: 0,
          behavior: 'smooth',
        });
      }
    };
    onMounted(() => {
      // eslint-disable-next-line prefer-destructuring
      componentName.value = window.location.hash.split('/')[1];
    });
    return {
      contentRef,
      components,
      componentName,
      handleClick,
    };
  },
});
