
import { defineComponent, ref } from 'vue';
import { Radio } from '@/components';
import Container from './component/container.vue';
import Attributes from './component/attributes.vue';

export default defineComponent({
  name: 'radioExample',
  components: {
    Container,
    Attributes,
    KiRadio: Radio,
    KiRadioGroup: Radio.Group,
  },
  setup() {
    const radio = ref('1');
    const radio1 = ref('2');
    const radio3 = ref('3');
    const radio4 = ref('1');
    const events = [
      {
        name: 'change',
        description: '绑定值变化时触发',
        type: '(value: string | number | boolean) => void',
      },
    ];
    const attribute = [
      {
        attribute: 'v-model',
        description: '绑定值',
        type: 'string | number | boolean',
        option: '——',
        default: '——',
      },
      {
        attribute: 'label',
        description: 'Radio 的 value',
        type: 'string | number | boolean',
        option: '——',
        default: '——',
      },
      {
        attribute: 'disabled',
        description: '是否禁用',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
      {
        attribute: 'border',
        description: '是否带有边框',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
    ];
    return {
      radio,
      radio1,
      radio3,
      radio4,
      events,
      attribute,
    };
  },
});
