
import { defineComponent } from 'vue';
import { Tag } from '@/components';
import Container from './component/container.vue';
import Attributes from './component/attributes.vue';

export default defineComponent({
  name: 'TagExample',
  components: {
    KiTag: Tag,
    Container,
    Attributes,
  },
  setup() {
    const events = [
      {
        name: 'on-close',
        description: '移除 Tag 时触发',
        type: '() => void',
      },
    ];
    const attributes = [
      {
        attribute: 'type',
        description: '按钮类型',
        type: 'string',
        option: 'primary | small | error | warning | info',
        default: 'primary',
      },
      {
        attribute: 'size',
        description: '按钮尺寸',
        type: 'string',
        option: 'mini | success | medium | large',
        default: 'medium',
      },
      {
        attribute: 'closable',
        description: '是否可移除',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
      {
        attribute: 'color',
        description: '标签背景颜色，设置后 type 将不起作用',
        type: 'string',
        option: '——',
        default: '——',
      },
      {
        attribute: 'font-color',
        description: '标签文字颜色，只有在设置了 color 属性后才能生效',
        type: 'string',
        option: '——',
        default: '——',
      },
    ];
    return {
      attributes,
      events,
    };
  },
});
