      {
        attribute: 'name',
        description: '上传的文件字段名',
        type: 'string',
        option: '——',
        default: 'file',
      },
      {
        attribute: 'action',
        description: '上传的文件地址',
        type: 'string',
        option: '——',
        default: '——',
      },
      {
        attribute: 'listType',
        description: '上传列表的展示方式',
        type: 'string',
        option: 'text | picture',
        default: 'text',
      },
      {
        attribute: 'withCredentials',
        description: '支持发送 cookie 凭证信息',
        type: 'boolean',
        option: 'false | true',
        default: 'false',
      },
      {
        attribute: 'autoUpload',
        description: '选择完文件后立刻上传',
        type: 'boolean',
        option: 'false | true',
        default: 'false',
      },
      {
        attribute: 'disabled',
        description: '是否禁用',
        type: 'boolean',
        option: 'false | true',
        default: 'false',
      },
      {
        attribute: 'showFileList',
        description: '是否显示上传的文件列表',
        type: 'boolean',
        option: 'false | true',
        default: 'false',
      },
      {
        attribute: 'fileList',
        description: '上传的文件列表，例如：[{name:"xx", url: "xx"}]',
        type: 'array',
        option: '——',
        default: '——',
      },
      {
        attribute: 'drag',
        description: '开启拖拽上传',
        type: 'boolean',
        option: 'false | true',
        default: 'false',
      },
      {
        attribute: 'multiple',
        description: '开启多选',
        type: 'boolean',
        option: 'false | true',
        default: 'false',
      },
      {
        attribute: 'limit',
        description: '限制上传文件数量',
        type: 'number',
        option: '——',
        default: '——',
      },
      {
        attribute: 'headers',
        description: '请求头信息',
        type: 'object',
        option: '——',
        default: '——',
      },
      {
        attribute: 'accept',
        description: '接受上传的文件类型',
        type: 'string',
        option: '——',
        default: '——',
      },
      {
        attribute: 'onSuccess',
        description: '上传成功的回调函数',
        type: '(response: unknown, file: FileRaw, fileList: FileRaw[]) => void',
        option: '——',
        default: '——',
      },
      {
        attribute: 'onProgress',
        description: '上传进度回调',
        type: '(event: UploadProgressEvent, file: FileRaw, fileList: FileRaw[]) => void',
        option: '——',
        default: '——',
      },
      {
        attribute: 'onError',
        description: '上传失败的回调函数',
        type: '(response: unknown, file: FileRaw, fileList: FileRaw[]) => void',
        option: '——',
        default: '——',
