
import { defineComponent } from 'vue';

import { Button } from '@/components';
import Attribute from './component/attributes.vue';
import Container from './component/container.vue';

export default defineComponent({
  components: {
    KiButton: Button,
    Container,
    Attribute,
  },
  setup() {
    const attributes = [
      {
        attribute: 'type',
        description: '按钮类型',
        type: 'string',
        option: 'primary | success | error | warning',
        default: '——',
      },
      {
        attribute: 'size',
        description: '按钮尺寸',
        type: 'string',
        option: 'small | medium | large',
        default: 'medium',
      },
      {
        attribute: 'icon',
        description: '按钮图标',
        type: 'string',
        option: '——',
        default: '——',
      },
      {
        attribute: 'round',
        description: '是否圆角',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
      {
        attribute: 'circular',
        description: '圆形按钮',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
      {
        attribute: 'disabled',
        description: '是否禁用',
        type: 'boolean',
        option: 'true | false',
        default: 'false',
      },
    ];
    return {
      attributes,
    };
  },
});
